import { getType } from 'typesafe-actions';
import { fetchCostModelsFailure, fetchCostModelsRequest, fetchCostModelsSuccess, updateFilterToolbar, } from './actions';
export const stateKey = 'costModels';
export const defaultState = {
    costModels: null,
    error: null,
    status: 0 /* none */,
    currentFilterType: 'name',
    currentFilterValue: '',
};
export const reducer = (state = defaultState, action) => {
    switch (action.type) {
        case getType(fetchCostModelsRequest):
            return Object.assign({}, state, { status: 1 /* inProgress */ });
        case getType(fetchCostModelsSuccess):
            return Object.assign({}, state, { currentFilterValue: '', status: 2 /* complete */, error: null, costModels: action.payload.data });
        case getType(fetchCostModelsFailure):
            return Object.assign({}, state, { status: 2 /* complete */, error: action.payload });
        case getType(updateFilterToolbar):
            return Object.assign({}, state, action.payload);
        default:
            return state;
    }
};
