import { fetchCostModels as apiGetCostModels, } from 'api/costModels';
import { createAsyncAction, createStandardAction } from 'typesafe-actions';
export const updateFilterToolbar = createStandardAction('fetch/costModels/filter')();
export const { request: fetchCostModelsRequest, success: fetchCostModelsSuccess, failure: fetchCostModelsFailure, } = createAsyncAction('fetch/costModels/request', 'fetch/costModels/success', 'fetch/costModels/failure')();
export const fetchCostModels = (query = '') => {
    return (dispatch) => {
        dispatch(fetchCostModelsRequest());
        return apiGetCostModels(query)
            .then(res => {
            dispatch(fetchCostModelsSuccess(res));
        })
            .catch(err => {
            dispatch(fetchCostModelsFailure(err));
        });
    };
};
